import React from 'react';

import MaterialTable from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableContainer from '@material-ui/core/TableContainer';
import Paper from '@material-ui/core/Paper';
import ArrowDown from '@material-ui/icons/ArrowDownwardOutlined';
import ArrowUp from '@material-ui/icons/ArrowUpwardOutlined';
import isNumberNegative from '../utils/isNumberNegative';

type TableProps = {
  columns: Array<{ value: string; align: any; width: string }>;
  title?: string;
  rows: Array<any>;
  color?: any;
};

const Table = ({ title, columns, rows }: TableProps) => {
  const renderCellValue = (value, delta) => {
    if (!delta) {
      return value;
    }

    return (
      <div>
        <div className="cell-value">
          {isNumberNegative(delta) ? (
            <>
              <ArrowDown style={{ fontSize: 12, color: '#F1C40F' }} />
              <span style={{ fontSize: 12 }}>{delta}</span>
            </>
          ) : (
            <>
              <ArrowUp style={{ fontSize: 12, color: '#00FF00' }} />
              <span style={{ fontSize: 12 }}>{delta}</span>
            </>
          )}
        </div>
        <div>{value}</div>
      </div>
    );
  };

  return (
    <div>
      <h3 className="table-title">{title}</h3>
      <TableContainer component={Paper}>
        <MaterialTable aria-label="customized table">
          <TableHead style={{ background: '#90caf9' }}>
            <TableRow>
              {columns.map(({ value, align, width }, index) => (
                <TableCell
                  key={index}
                  style={{ color: 'black' }}
                  align={align}
                  width={width}
                >
                  {value}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={index}>
                {row.map(({ value, delta, align }, index) => (
                  <TableCell key={index} align={align}>
                    {renderCellValue(value, delta)}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </MaterialTable>
      </TableContainer>
    </div>
  );
};

export default Table;
